export class HttpService {

    _handleErrors(res){
        if(!res.ok) throw new Error(res.statusText)
        return res
    }
    
    get(url) {
        
        return fetch(url)
            .then(res => this._handleErrors(res))
            .then(res => res.json())
    }
    
    post(url, dado) {


        return fetch(url, {
            headers: {'Content-type' : 'application/json'},
            method: 'post',
            body: JSON.stringify(dado) // usando JSON.stringifly para converter objeto em uma string no formato JSON.
        })
        .then(res => this._handleErrors(res))

    }
}